<template>
  <va-card :title="$t('industryOverview.suggestedShares') + industry">
    <va-data-table
      :fields="fields"
      :data="shares"
      class="va-table--hoverable"
      no-pagination
      clickable
      @row-clicked="redirect"
    >
      <template slot="trend" slot-scope="props">
        <va-icon :name="getTrendIcon(props.rowData.chgPct)" :color="getTrendColor(props.rowData.chgPct)" />
      </template>

      <template slot="chgPct" slot-scope="props">
        <div :style="'color:' + getChgPctColor(props.rowData.chgPct)">
          {{ getChgPct(props.rowData.chgPct) }}
        </div>
      </template>

    </va-data-table>
  </va-card>
</template>

<script>
import allShares from '../../data/allShares.json'

export default {
  computed: {
    industry () {
      return this.$store.state.app.industry.industryName
    },
    shares () {
      var industry = this.$store.state.app.industry.industryCode

      if (industry === 'petroleumGasesEquipmentServices') {
        return allShares.petroleumGasesEquipmentServices
      }
      if (industry === 'funeral') {
        return allShares.funeral
      }
      if (industry === 'semiconductors') {
        return allShares.semiconductors
      }

      return []
    },
    fields () {
      return [
        {
          name: '__slot:trend',
          width: '2%',
          dataClass: 'text-center',
        }, {
          name: 'sharesSymbol',
          title: '',
          width: '8%',
          dataClass: 'text-right',
        }, {
          name: 'sharesName',
          title: this.$t('industryOverview.headings.sharesName'),
          width: '50%',
        }, {
          name: 'lastPrice',
          title: this.$t('industryOverview.headings.lastPrice'),
          sortField: 'lastPrice',
          width: '8%',
          dataClass: 'text-right',
        }, {
          name: '__slot:chgPct',
          title: this.$t('industryOverview.headings.chgPct'),
          sortField: 'chgPct',
          width: '8%',
          dataClass: 'text-right',
        }, {
          name: 'volume',
          title: this.$t('industryOverview.headings.volume'),
          sortField: 'volume',
          width: '8%',
          dataClass: 'text-right',
          sortable: true,
        }, {
          name: 'turnover',
          title: this.$t('industryOverview.headings.turnover'),
          sortField: 'turnover',
          width: '8%',
          dataClass: 'text-right',
          sortable: true,
        }, {
          name: 'peRatio',
          title: this.$t('industryOverview.headings.peRatio'),
          sortField: 'peRatio',
          width: '8%',
          dataClass: 'text-right',
        },
      ]
    },
  },
  methods: {
    getTrendIcon (chgPct) {
      if (chgPct > 0) {
        return 'fa fa-caret-up'
      }

      if (chgPct < 0) {
        return 'fa fa-caret-down'
      }

      return 'fa fa-minus'
    },
    getTrendColor (chgPct) {
      if (chgPct > 0) {
        return 'primary'
      }

      if (chgPct < 0) {
        return 'danger'
      }

      return 'grey'
    },
    getChgPct (chgPct) {
      if (chgPct > 0) {
        return '+' + chgPct + '%'
      }

      return chgPct + '%'
    },
    getChgPctColor (chgPct) {
      if (chgPct > 0) {
        // green
        return 'rgb(64, 229, 131)'
      }

      if (chgPct < 0) {
        // red
        return 'rgb(227, 75, 74)'
      }

      return 'grey'
    },
    redirect (sharesInfo) {
      var sharesName = sharesInfo.sharesName
      var sharesSymbol = sharesInfo.sharesSymbol

      this.$store
        .dispatch('setSharesAPI', {
          sharesName,
          sharesSymbol,
        })
        .then(() => {
          this.$router.push({
            name: 'shares-overview',
          })
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
        })
    },
  },
}
</script>

